import Layout from './layout.js';
import Link from 'next/link';

const headerStyle = {
    padding: 5
};

const Header = () => (
    <div style = {headerStyle}>
            <p>
                <Link href = '/index'>Home</Link>
                <Link href = '/login'>Login</Link>
                <Link href = '#'>Sign Up</Link>
    </p>
    </div>
);

export default Header;